import logo from './logo.svg';
import './App.css';
import React, { useState, useEffect } from 'react'
import './App.css'; 
import MapComponent from './MapComponent'; 
import { MapContainer, TileLayer } from "react-leaflet";
import "./App.css";
import "leaflet/dist/leaflet.css";
import citiesData from './data.json';
import Routing from "./Routing";
import TextField from '@mui/material/TextField';
import Autocomplete from '@mui/material/Autocomplete';
import Paper from '@mui/material/Paper';
import { makeStyles } from '@mui/styles'; 


const useStyles = makeStyles((theme) => ({
  inputRoot: {
    color: 'yellow',
    '& .MuiOutlinedInput-notchedOutline': {
      borderColor: 'white',
    },
    '&:hover .MuiOutlinedInput-notchedOutline': {
      borderColor: 'white',
    },
    '&.Mui-focused .MuiOutlinedInput-notchedOutline': {
      borderColor: 'white',
    },
  },
}));

export default function App() {
  const classes = useStyles();
  const position = [20.593683, 78.962883];

  const [buildingsData, setBuildingsData] = useState([]);  // define buildings data
  const [sourceBuilding, setSourceBuilding] = useState(null);  // define source building
  const [destinationBuilding, setDestinationBuilding] = useState(null);  // define destination building

  useEffect(() => {
    // Fetch or define buildings data here
    const buildings = [
      { building: 'The Hub', location: '51.893172573410574, -8.493300676345827', id: 1 },
      { building: 'The Kane', location: '51.89275544326038, -8.493922948837282', id: 2 },
      // Add more buildings
    ];
    setBuildingsData(buildings);
  }, []);

  return (
    <div className="leaflet-container">
      <div className="container">
        {/* Source Building Autocomplete */}
        <Autocomplete
          id="combo-box-demo"
          options={buildingsData}
          onChange={(event, value) => setSourceBuilding(value)}  // update source building
          classes={classes}
          size="small"
          PaperComponent={({ children }) => (
            <Paper style={{ background: 'yellow' }} elevation={10}>
              {children}
            </Paper>
          )}
          getOptionLabel={(option) => `${option.building}`}
          style={{ width: 300, paddingBottom: '5%' }}
          renderInput={(params) => (
            <TextField
              {...params}
              color="secondary"
              label="Source Building"
              variant="outlined"
              InputLabelProps={{
                style: { color: 'white' },
              }}
            />
          )}
        />
        
        {/* Destination Building Autocomplete */}
        <Autocomplete
          id="combo-box-demo1"
          classes={classes}
          onChange={(event, value) => setDestinationBuilding(value)}  // update destination building
          options={buildingsData}
          size="small"
          PaperComponent={({ children }) => (
            <Paper style={{ background: 'yellow' }} elevation={10}>
              {children}
            </Paper>
          )}
          getOptionLabel={(option) => `${option.building}`}
          style={{ width: 300, paddingBottom: '5%' }}
          renderInput={(params) => (
            <TextField
              {...params}
              color="secondary"
              label="Destination Building"
              variant="outlined"
              InputLabelProps={{
                style: { color: 'white' },
              }}
            />
          )}
        />
      </div>

      <MapContainer center={position} zoom={6} style={{ height: '100%', width: '100%' }}>
        <TileLayer
          attribution="&copy; OpenStreetMap contributors"
          url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
        />
        <Routing sourceBuilding={sourceBuilding} destinationBuilding={destinationBuilding} />
      </MapContainer>
    </div>
  );
}
